(function initSecurityAlerts () {

	"use strict";

	/**
	 * redirect old URL hashes to new URLs
	 */
	function handleHashChanage () {

		// handle hashes for vendors
		if (location.hash) {

			let match;

			match = location.hash.match(/^#bulletins-(\w+)/);
			if (match) {

				location.href = location.href.replace(location.hash, match[1] + "/");

			}

		}

	}

	document.addEventListener("DOMContentLoaded", handleHashChanage);
	window.addEventListener("hashchange", handleHashChanage);

	/**
	 * handleClick - toggle bulletins open/closed
	 * @this {Element} the target of the event
	 * @returns {undefined}
	 */
	function handleClick (event) {

		event.target.parentElement.classList.toggle("closed");

	}


	/**
	 * handleReady - close bulletins and bind click handler
	 * @returns {undefined}
	 */
	function handleReady () {

		var bulletins, controls, index;

		// close open bulletins
		bulletins = document.querySelectorAll(".js-bulletin");
		for (index = 0; index < bulletins.length; index += 1) {

			bulletins.item(index).classList.add("closed");

		}

		// set bulletin expander controls
		controls = document.querySelectorAll(".js-bulletin-control");
		for (index = 0; index < controls.length; index += 1) {

			controls.item(index).addEventListener("click", handleClick);

		}

	}

	document.addEventListener("DOMContentLoaded", handleReady);

}());
